import axios from 'axios'

const API_BASE_URL = '';

export default {
    getCategories() {
        return axios.get(API_BASE_URL + '/categories');
    },
    getPlayers() {
        return axios.get(API_BASE_URL + '/players');
    },
    addGame(gameToAdd) {
        return axios.post(API_BASE_URL + '/games', gameToAdd);
    },
    getQuestions(categoryId) {
        const url = `${API_BASE_URL}/categories/${categoryId}/questions`;
        return axios.get(url);
    },
    updateScores(gameInfo) {
        axios.put(API_BASE_URL + "/games/" + gameInfo.gameId + '?saveWinners=true', gameInfo)
    }
}