<template>

  <div id="answerpane" class="answerpane" @click="setAnswer">

    <div class="answer-text">{{ answerText }}</div>

  </div>
</template>

<script>
export default {
  props: ['answer'],
  computed: {
    answerText() {
      return unescape(this.answer);
    },
    blinkCount() {
      return this.$store.state.blinkCount;
    },
    roundDone() {
      return this.$store.state.roundDone;
    },
  },
  methods: {
    setAnswer() {
      if (this.blinkCount <= 0 && !this.roundDone) {
        this.$store.commit('UPDATE_ANSWER_CHOICE', this.answer);
        this.$store.commit('UPDATE_WAITING_FOR_ANSWER', false);
      }
    },
  },
};
</script>

<style scoped>
.answerpane {
  width: 100%;
  height: 55px;
  background-color: var(--panel-color);
  border-radius: 25px;
  margin-bottom: 10px;
  color: #000000;
  padding-left: 15px;
  cursor: default;

  display: flex;
  align-items: center;
  font-weight: bold;
}

.answerpane:hover {
  background-color: var(--hover-bg-color);

  color: var(--hover-text-color);
}
</style>