<template>
  <div id="main-container">
    <header>
      <div class="banner-container">
        <img
          src="../assets/Gold-Banner--Arvin61r58.svg"
          id="header-banner-img"
        />

        <div class="header-text">TE Trivia Bonanza</div>

        <section id="results-container">
          <div
            id="results-section"
            v-show="showResultPane"
            :class="{
              'result-correct': answerCorrect,
              'result-incorrect': !answerCorrect,
            }"
          >
            <div id="result-pane">
              <span id="result-text">{{
                answerCorrect ? "Correct!" : "Sorry, that's incorrect."
              }}</span>
            </div>
          </div>
        </section>
      </div>
    </header>

    <div class="dividing-line">&nbsp;</div>

    <main>
      <div id="middle-section">
        <div id="questions-section">
          <question-pane :question="curQuestion" v-if="!loading" />
          <h2 class="loading-msg" v-if="loading">Loading...</h2>
        </div>

        <div id="player-info-section" v-show="!loading">
          <player-info
            :playerName="currentPlayer ? currentPlayer.name : ''"
            :qNum="qNum"
            :curScore="score"
            :maxQs="numOfQs"
          />
        </div>
      </div>
    </main>
    
    <footer class="nav-link-section">
      <router-link :to="{name: 'game-setup'}" class="custom-btn" v-show="!loading">{{ gameDone ? 'New Game' : 'Cancel' }}</router-link>
    </footer>
  </div>
</template>

<script>
import PlayerInfo from "../components/PlayerInfo.vue";
import QuestionPane from "../components/QuestionPane.vue";
import triviaService from '@/services/TriviaApiService';
export default {
  components: { QuestionPane, PlayerInfo },
  data() {
    return {
      qCount: 0,
      curQuestion: {
        answers: [],
      },
      questions: [],
      loading: true,
    };
  },
  computed: {
    score() {
      return this.$store.state.score;
    },
    qNum() {
      return this.$store.state.qNum;
    },
    numOfQs() {
      return this.$store.state.numOfQs;
    },
    showResultPane() {
      return this.$store.state.showResultPane;
    },
    answerCorrect() {
      return this.$store.state.answerCorrect;
    },
    blinkCount() {
      return this.$store.state.blinkCount;
    },
    players() {
      return this.$store.state.players;
    },
    playerIndex() {
      return this.$store.state.playerIndex;
    },
    currentPlayer() {
      return this.$store.state.currentPlayer;
    },
    showGame() {
      return this.$store.state.showGame;
    },
    gameDone() {
      return this.$store.state.gameDone;
    }
  },
  methods: {
    startGame() {
      this.$store.commit("RESET_GAME");
      this.$store.commit("UPDATE_Q_NUM", 1);
      this.$store.commit(
        "UPDATE_CURRENT_PLAYER",
        this.players[this.playerIndex]
      );

      // get questions from api for the categoryid in the store
      triviaService.getQuestions(this.$store.state.categoryId).then( response => {

      // when the questions are available, set the data property questions to the result data,
      // set loading to false, then call loadRandomQuestion
        this.questions = response.data;
        this.loading = false;
        this.loadRandomQuestion();
      });
    },
    shuffleQuestions() {
      this.questions.sort(() => Math.random() - 0.5);
    },
    loadRandomQuestion() {
      if (!this.loading) {
        this.shuffleQuestions();
        this.curQuestion = this.questions[this.qNum - 1];
        this.curQuestion.answers = this.getAnswers(this.curQuestion);
        this.$store.commit("UPDATE_WAITING_FOR_ANSWER", true);
      }
    },
    getAnswers(question) {
      let answers = [];

      answers.push(this.curQuestion.correctAnswer);

      for (let i = 0; i < question.incorrectAnswers.length; i++) {
        answers.push(question.incorrectAnswers[i]);
      }

      answers.sort(() => Math.random() - 0.5);

      return answers;
    }
  },
  created() {
    this.startGame();
  },
  watch: {
    qNum(val) {
      if (!this.loading) {
        const maxQs = this.$store.state.numOfQs;
        if (val <= maxQs) {
          this.loadRandomQuestion();
        }
      }
    },
    playerIndex(val) {
      this.$store.commit("UPDATE_CURRENT_PLAYER", this.players[val]);
      this.$store.commit("UPDATE_SCORE", 0);
      this.$store.commit("UPDATE_Q_NUM", 1);
      this.$store.commit("UPDATE_ROUND_DONE", false);
      this.loadRandomQuestion();
    },
    showGame(val) {
      if (val == true) {
        this.$store.commit(
          "UPDATE_CURRENT_PLAYER",
          this.players[this.playerIndex]
        );
        this.$store.commit("UPDATE_MORE_PLAYERS", true);
        this.$store.commit("UPDATE_Q_NUM", 1);
        this.loadRandomQuestion();
      }
    },
  },
};
</script>

