import Vue from 'vue'
import VueRouter from 'vue-router'
// import GameSetup from '../views/GameSetup'
import GameSetup from '@/views/GameSetup'
import PlayGame from '@/views/PlayGame'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'game-setup',
    component: GameSetup
  },
  {
    path: '/play',
    name: 'play-game',
    component: PlayGame
  }
]

const router = new VueRouter({
  routes
})

export default router
