<template>
  <section id="player-section">
    <div id="player-info-container">
      <div class="player-info-item">
        Current Player: <span id="current-player">{{ playerName }}</span>
      </div>
      <div class="player-info-item">
        Question #<span id="qNum">{{ qNum }}</span>
      </div>
      <div class="player-info-item">
        <span id="final-score" v-show="roundDone">Final </span>Score:

        <span id="curScore">{{ curScore }}</span>
        /
        <span id="qCount">{{ maxQs }}</span>
        <a
          id="next-btn"
          class="custom-btn"
          v-show="roundDone"
          @click="nextPlayer"
          >{{ morePlayers ? "Next Player" : "Final Scores" }}</a
        >
      </div>
    </div>
  </section>
</template>

<script>
import triviaApiService from '../services/TriviaApiService';
export default {
  props: ["playerName", "qNum", "curScore", "maxQs"],
  computed: {
    roundDone() {
      return this.$store.state.roundDone;
    },
    morePlayers() {
      return this.$store.state.morePlayers;
    },
  },
  methods: {
    nextPlayer() {
      const playerIndex = this.$store.state.playerIndex;
      const maxPlayers = this.$store.state.players.length;

      if (this.morePlayers) {
        if (playerIndex < maxPlayers - 1) {
          this.$store.commit("UPDATE_PLAYER_INDEX", playerIndex + 1);
        }

        if (playerIndex + 1 >= maxPlayers - 1) {
          this.$store.commit("UPDATE_MORE_PLAYERS", false);
          triviaApiService.u
        }
      } else {
        alert(
          "Final Scores for game " +
            this.$store.state.gameName +
            ":\n\n" +
            this.getFinalScoresString()
        );
      }
    },
    getFinalScoresString() {
      let scoresString = "";

      this.$store.state.scores.forEach((score) => {
        scoresString += `${score.player.username}: ${score.score}\n`;
      });
      return scoresString;
    },
  },
};
</script>

