<template>
  <div class="formfield" id="userselect">
    <select id="addplayer" name="addplayer" class="selectbox" size="4">
          <option v-on:click="toggleUserSelection(user)" v-for="user in userlist" :key="user.id" :value="user.name">{{ user.name }}</option>
    </select>
  </div>
</template>

<script>
export default {
  name: "selection-box",
  data() {
    return {};
  },
  props: ["userlist", "mutation"],
  methods: {
    toggleUserSelection(linda) {
      this.$store.commit(this.mutation, linda);
    },
  },
};
</script>

<style scoped>
.formfield {
  padding: 10px 0 10px 15px;
  color: black;
  font-weight: bold;
  background-color: var(--panel-color);
}

.selectbox {
  background-color: var(--bg-color);
  width: 100%;
  color: var(--panel-color);
  text-align: center;
  padding: 5px;
}
