<template>
  <div id="container">
    <header>
      <div class="banner-container">
        <img
          src="../assets/Gold-Banner--Arvin61r58.svg"
          id="header-banner-img"
        />

        <div class="header-text">TE Trivia Bonanza</div>
      </div>
    </header>

    <div class="dividing-line">&nbsp;</div>

    <main class="main-section">
      <div id="panel-container">
        <div class="gameGrid">
          <div class="formfield" id="gameNamesection">
            <label for="gameName">Give your game a name: </label>
            <input
              type="text"
              id="gameName"
              name="gameName"
              class="textinput"
              v-model="gameName"
            />
          </div>

          <div class="formfield" id="numQuestionSection">
            <input
              type="number"
              min="1"
              max="10"
              value="2"
              id="qCount"
              name="qCount"
              class="numberinput"
              v-model.number="numOfQs"
            />

            <label for="qCount">How many questions per round?</label>
          </div>

          <category-list
            :categories="categories"
            label-text="Select a category: "
            class="formfield selectinput"
          />

          <div class="formfield" id="addplayersection">
            <selection-box
              class="selectinput"
              v-bind:userlist="$store.state.remainingUsers"
              mutation="SELECT_REMAINING_USER"
            />
          </div>
          <a
            href="playGame.html"
            class="custom-btn start-game-btn"
            id="play-btn"
            @click.prevent="saveGameInfo"
            >Start Game</a
          >

          <div class="selected-players-heading">Selected Players</div>

          <div class="formfield selectinput" id="selectedPlayers">
            <selection-box
              class="selectinput"
              v-bind:userlist="$store.state.players"
              mutation="SELECT_PLAYER"
            />
          </div>
          <div id="arrow-container">
            <img
              id="left-arrow"
              src="../assets/left-arrow.png"
              class="arrowImg"
              @click="
                moveSelectedUsers(
                  $store.state.players,
                  $store.state.remainingUsers,
                  false
                )
              "
            />
            <img
              id="right-arrow"
              src="../assets/right-arrow.png"
              class="arrowImg"
              @click="
                moveSelectedUsers(
                  $store.state.remainingUsers,
                  $store.state.players,
                  true
                )
              "
            />
          </div>
        </div>
      </div>
    </main>
  </div>
</template>

<script>

import SelectionBox from "../components/SelectionBox.vue";
import CategoryList from "@/components/CategoryList.vue";
import triviaService from '@/services/TriviaApiService'
export default {
  name: "game-setup",
  components: { SelectionBox, CategoryList },
  data() {
    return {
      users: [],
      players: [],
      gameName: "",
      numOfQs: 2,
    };
  },
  computed: {
    categories() {
      return this.$store.state.categories;
    },
  },
  methods: {
    copyUsers(origUsers) {
      const copiedUsers = [];
      origUsers.forEach((user) => {
        copiedUsers.push(user);
      });
      return copiedUsers;
    },
    moveSelectedUsers(fromList, toList, fromUsers) {
      let from = this.copyUsers(fromList);
      let to = this.copyUsers(toList);

      from.forEach((user) => {
        let newUser = {
          id: user.id,
          name: user.name,
        };
        if (user.isSelected) {
          newUser.isSelected = false;
          if (!to.includes(newUser)) {
            to.push(newUser);
          }
        }
      });
      from = from.filter((user) => {
        return !user.isSelected;
      });
      if (fromUsers) {
        this.$store.commit("UPDATE_REMAINING_USERS", from);
        this.$store.commit("UPDATE_PLAYERS", to);
      } else {
        this.$store.commit("UPDATE_PLAYERS", from);
        this.$store.commit("UPDATE_REMAINING_USERS", to);
      }
    },
    createGame() {
      const newGame = {
        gameName: this.$store.state.gameName,
        apiCategoryId: this.$store.state.categoryId,
        categoryName: this.$store.getters.categoryName,
        totalQuestions: this.$store.state.numOfQs,
        playerScores: [],
      };
      this.$store.state.players.forEach((player) => {
        const score = {
          player: {
            playerId: player.id,
          },
          score: 0,
        };
        newGame.playerScores.push(score);
      });

      // add code to createGame using API here
      triviaService.addGame(newGame).then( resp => {

        // set the current game in the store from the response
        this.$store.commit('SET_CUR_GAME', resp.data);

        // once game is created, go to PlayGame view
        this.$router.push( { name: 'play-game'  });

      })
      
    },
    saveGameInfo() {
      if (
        this.gameName === undefined ||
        this.gameName === null ||
        this.gameName.trim() == ""
      ) {
        alert("Please enter a game name.");
        return false;
      }

      if (this.$store.state.categoryId == "") {
        alert("Please select a category for your game.");
        return false;
      }

      if (
        this.$store.state.players === undefined ||
        this.$store.state.players == null ||
        this.$store.state.players.length < 1
      ) {
        alert("Please select players for the game.");
        return false;
      }

      /* store the data in the store
          * set the game name
          * set the number of questions
          * set the showGame prop in the store to true to indicate we are
             switiching to the game "screen"
      */
      this.$store.commit("UPDATE_GAME_NAME", this.gameName);
      this.$store.commit("UPDATE_NUM_OF_QS", this.numOfQs);
      this.$store.commit("UPDATE_SHOW_GAME", true);
      this.createGame();
    },
    handleError(err, msg) {
      if (err.response) {
        alert(msg + " Error status: " + err.response.status);
      } else {
        alert(msg);
      }
    },
  },
  created() {
    this.$store.state.users.forEach((user) => {
      this.users.push({ id: user.id, name: user.name, isSelected: false });
    });
    this.$store.commit("UPDATE_PLAYERS", []);
    if (this.$store.state.categories.length == 0) {
      // add code to get categories from API.
      triviaService.getCategories().then( response => {
        
      // once the categories are available, set them in the store

        this.$store.commit('SET_CATEGORIES', response.data)
      })

    }
    if (this.$store.state.users.length == 0) {
      // add code to get players from API.
      triviaService.getPlayers().then( response => {

      // once the players are available, add this code
          
          const users = [];
          response.data.forEach((user) => {
            users.push({
              id: user.playerId,
              name: user.username,
              isSelected: false,
            });
          });
          this.$store.commit("UPDATE_USERS", users);
          this.$store.commit("UPDATE_REMAINING_USERS", users);
          
      })
    } else {
      this.$store.commit("UPDATE_REMAINING_USERS", this.users);
    }
  },
};
</script>

