<template>
  <section id="trivia-section">
    <div id="question" class="question">
      {{ questionText }}
    </div>

    <div id="answers" class="answers">
      <answer-pane
        v-for="(curAnswer, index) in question.answers"
        :key="index"
        :answer="curAnswer"
      />
    </div>
  </section>
</template>

<script>
import AnswerPane from "./AnswerPane.vue";
import triviaService from '../services/TriviaApiService'
export default {
  components: { AnswerPane },
  props: ["question"],
  data() {
    return {};
  },
  computed: {
    questionText() {
      return unescape(this.question.question);
    },
    waitingForAnswer() {
      return this.$store.state.waitingForAnswer;
    },
    blinkCount() {
      return this.$store.state.blinkCount;
    },
  },
  watch: {
    waitingForAnswer(val) {
      this.handleWaitingForAnswerUpdate(val);
    },
  },
  methods: {
    getWinners() {
      let winners = [];

      let highScore;
      this.$store.state.curGame.playerScores.forEach((playerScore) => {
        const winner = {
          winnerId: playerScore.player.playerId,
          gameId: this.$store.state.curGame.gameId,
        };
        if (!highScore || playerScore.score > highScore) {
          highScore = playerScore.score;
          winners = [winner];
        } else {
          winners.push(winner);
        }
      });
      return winners;
    },
    startResultsSectionBlink() {
      this.$store.commit("UPDATE_BLINK_COUNT", 3);
      this.showResultsSection();
    },
    showResultsSection() {
      this.$store.commit("UPDATE_SHOW_RESULT_PANE", true);
      setTimeout(this.hideResultsSection, 500);
    },
    hideResultsSection() {
      this.$store.commit("UPDATE_SHOW_RESULT_PANE", false);
      this.$store.commit("UPDATE_BLINK_COUNT", this.blinkCount - 1);

      /* if we need to blink more, wait 500 milliseconds and call showResultsSection */
      if (this.blinkCount > 0) {
        setTimeout(this.showResultsSection, 500);
      } else {
        /* otherwise move on */
        if (this.$store.state.answerCorrect == true) {
          const score = this.$store.state.score;
          this.$store.commit("UPDATE_SCORE", score + 1);
        }

        const qNum = this.$store.state.qNum;
        const numOfQs = this.$store.state.numOfQs;
        if (qNum < numOfQs) {
          this.$store.commit("UPDATE_Q_NUM", qNum + 1);
        } else {
          this.$store.commit("UPDATE_ROUND_DONE", true);
          const newScore = {
            player: {
              playerId: this.$store.state.currentPlayer.id,
              username: this.$store.state.currentPlayer.name,
            },
            score: this.$store.state.score,
          };
          this.$store.commit("ADD_SCORE", newScore);
          if (
            this.$store.state.scores.length == this.$store.state.players.length
          ) {
            this.$store.commit("UPDATE_GAME_DONE", true)
            this.$store.commit("UPDATE_GAME_SCORES");

            // update the game scores using the API
            triviaService.updateScores(this.$store.state.curGame)

            // .then( resp => {
            //   if (resp.status != 200) {
            //     alert("Issue saving final scores");
            //   }
            // })
          }
        }
      }
    },
    setAnswerCorrect(isCorrect) {
      this.$store.commit("UPDATE_ANSWER_CORRECT", isCorrect);
    },
    handleWaitingForAnswerUpdate(waiting) {
      if (!waiting) {
        // console.log(this.$store.state.answerText);
        this.$store.commit("UPDATE_WAITING_FOR_ANSWER", false);
        if (this.$store.state.answerText == this.question.correctAnswer) {
          this.$store.commit("UPDATE_ANSWER_CORRECT", true);
        } else {
          this.$store.commit("UPDATE_ANSWER_CORRECT", false);
        }
        this.startResultsSectionBlink();
      }
      return waiting;
    },
    handleError(err, msg) {
      if (err.response) {
        alert(msg + " Error status: " + err.response.status);
      } else {
        alert(msg);
      }
    },
  },
};
</script>

<style scope>
.question {
  color: var(--panel-color);
  font-size: 18px;
  text-align: left;

  padding: 10px;
}
</style>